<template>
  <div class="container">
    <van-cell
      v-for="(item, index) in outline"
      :key="index"
      @click="onClick(item.path)"
    >
      {{ index + 1 }}、{{ item.title }}
    </van-cell>
  </div>
</template>
<script>
export default {
  data() {
    return {
      outline: [
        {
          title: "单词列表",
          path: "/vocabulary/list",
        },
        {
          title: "单词听写",
          path: "/vocabulary/dictation",
        },
        {
          title: "视频教程",
          path: "/videos/index",
        },
      ],
    };
  },
  created() {},
  methods: {
    onClick(path) {
      this.$router.push(path);
    },
  },
};
</script>
<style lang="scss" scoped>
</style>